import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Proftitle } from '../entity';
import {ProftitleService} from '../proftitle.service';

@Component({
  selector: 'app-update-proftitle',
  templateUrl: './update-proftitle.component.html',
  styleUrls: ['./update-proftitle.component.css']
})
export class UpdateProftitleComponent implements OnInit {
  @Input() proftitle: Proftitle;

  constructor(
    private route: ActivatedRoute,
    private proftitleService: ProftitleService,
    private location: Location
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.proftitleService.getProftitle(id)
      .subscribe(res => this.proftitle = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.proftitleService.updateProftitle(this.proftitle)
      .subscribe(() => this.goBack());
  }
}
