import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { School } from '../entity';
import {SchoolService} from '../school.service';

@Component({
  selector: 'app-update-school',
  templateUrl: './update-school.component.html',
  styleUrls: ['./update-school.component.css']
})
export class UpdateSchoolComponent implements OnInit {
  @Input() school: School;

  constructor(
    private route: ActivatedRoute,
    private schoolService: SchoolService,
    private location: Location
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.schoolService.getSchool(id)
      .subscribe(res => this.school = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.schoolService.updateSchool(this.school)
      .subscribe(() => this.goBack());
  }
}
