import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {DepartmentService} from '../department.service';
import {SchoolService} from '../school.service';
import {School, Department1, Department} from '../entity';
@Component({
  selector: 'app-add-department',
  templateUrl: './add-department.component.html',
  styleUrls: ['./add-department.component.css']
})
export class AddDepartmentComponent implements OnInit {
  @Input() schools: School[];
  department: Department1;
  school: School;
  constructor(
    private route: ActivatedRoute,
    private departmentService: DepartmentService,
    private schoolService: SchoolService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getSchools() ;
  }
  getSchools(): void {
    this.schoolService.getSchools ()
      .subscribe(res => this.schools = res);
  }

  save(description: string, no: string, school: School, remarks: string): void { //多个文本框数据
    this.department = {no: no.trim(), description: description.trim(), school: this.school, remarks: remarks.trim()};
    if (!description) { return; }
    this.departmentService.addDepartment(this.department)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}
