import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { TeacherService} from '../teacher.service';
import { SchoolService} from '../school.service';
import { DegreeService} from '../degree.service';
import { ProftitleService} from '../proftitle.service';
import { DepartmentService} from '../department.service';
import { Teacher, Degree, Proftitle, School,  Department } from '../entity';

@Component({
  selector: 'app-update-teacher',
  templateUrl: './update-teacher.component.html',
  styleUrls: ['./update-teacher.component.css']
})
export class UpdateTeacherComponent implements OnInit {
  @Input() schools: School[];
  degrees: Degree[];
  proftitles: Proftitle[];
  departments: Department[];
  teacher: Teacher;
  school: School;
  degree: Degree;
  proftitle: Proftitle;
  department: Department;
  constructor(
    private route: ActivatedRoute,
    private teacherService: TeacherService,
    private schoolService: SchoolService,
    private degreeService: DegreeService,
    private proftitleService: ProftitleService,
    private departmentService: DepartmentService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getSchools();
    this.getDegrees();
    this.getProftitles();
    this.getDepartments();
    const id = this.route.snapshot.params.id; // 要修改专业的id
    this.teacherService.getTeacher(id)
      .subscribe(res => {
        this.teacher = res;
      });
  }
  getSchools(): void {
    this.schoolService.getSchools()
      .subscribe(res => this.schools = res);
  }
  getDegrees(): void {
    this.degreeService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  getProftitles(): void {
    this.proftitleService.getProftitles ()
      .subscribe(res => this.proftitles = res);
  }
  getDepartments(): void {
    this.departmentService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  getDepSch(id: number): void {
    this.departmentService.getDepSch (id)
      .subscribe(res => this.departments = res);
  }

  save(): void { //多个文本框数据
    this.teacherService.updateTeacher(this.teacher)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
  compareFn(o1: School, o2: School): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

}
