import {Component, OnInit, Input, Output} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {DepartmentService} from '../department.service';
import {SchoolService} from '../school.service';
import {School, Department} from '../entity';
@Component({
  selector: 'app-update-department',
  templateUrl: './update-department.component.html',
  styleUrls: ['./update-department.component.css']
})
export class UpdateDepartmentComponent implements OnInit {
  @Input() schools: School[];
  department: Department;


  constructor(
    private route: ActivatedRoute,
    private departmentService: DepartmentService,
    private schoolService: SchoolService,
    private location: Location
  ) {
  }

  ngOnInit() {
    this.getSchools(); // 下拉菜单赋值
    const id = this.route.snapshot.params.id; // 要修改专业的id
    this.departmentService.getDepartment(id)
      .subscribe(res => {
        this.department = res;
      });
  }

  compareFn(o1: School, o2: School): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  getSchools(): void {
    this.schoolService.getSchools()
      .subscribe(res => this.schools = res);
  }

  save(): void { // 多个文本框数据
    console.log(this.department);
    this.departmentService.updateDepartment(this.department)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
}
